<template>
  <div class="app-wrapper">

    <div class="buttons-wrapper">
      <button
        @click="openOldReport"
        :class="{'selected-button': !isNewReport}"
        class="report-type-button"
      >
      Старый отчет</button>
      <button 
        @click="openNewReport"
        :class="{'selected-button': isNewReport}"
        class="report-type-button"
      >Новый отчет</button>
    </div>

    <oldReport
      v-if="!isNewReport"
    />

    <newReport
      v-if="isNewReport"
    />

  </div>
</template>

<script>
import oldReport from '@/components/oldReport.vue'
import newReport from '@/components/newReport.vue'

export default {
  data() {
    return {
      isNewReport: false
    }
  },
  components: {
    oldReport,
    newReport
  },
  methods: {
    openOldReport(){
      this.isNewReport = false
    },
    openNewReport(){
      this.isNewReport = true
    }
  },

}

</script>

<style>
  .app-wrapper{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .buttons-wrapper{
    display: flex;
    justify-content: start;
    width: 1200px;
  }
  .selected-button{
    background-color: lightgreen;
  }
  .report-type-button{
    width: 130px;
    height: 30px;
    margin: 5px;
  }
</style>
